import React from "react";
import CodeSlide from 'spectacle-code-slide';
import code from './code.example';


export default (
  <CodeSlide
  transition={["fade"]}
  lang="js"
  code={code}
  ranges={[
    { loc: [0, 270], title: "Walking through some code" },
    { loc: [0, 1], title: "The Beginning" },
    { loc: [1, 2] },
    { loc: [1, 2], note: "Heres a note!" },
    { loc: [2, 3] },
    { loc: [8, 10] },
    // ...
  ]}/>
);


